class LightBox {
  constructor(config = {}) {
    this.modal = null;
    this.groupItems = null;
    this.preview = config.preview || true;
    this.overflow = config.overflow || true;
    this.arrowLeft = config.arrowLeft || "";
    this.arrowRight = config.arrowRight || "";
    this.items = document.querySelectorAll("[data-lb-item]");

    if (this.items.length > 0) this.init();
  }

  // –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è
  init() {
    // –¥–æ–±–∞–≤–ª—è–µ—Ç –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É
    this.renderModal();

    // —Å–ª—É—à–∞–µ–º –∫–ª–∏–∫ –ø–æ –∫–∞–∂–¥–æ–º—É —ç–ª–µ–º–µ–Ω—Ç—É data-lb-item
    this.items.forEach((item) => {
      item.addEventListener("click", () => this.itemHandler(item));
    });

    // —Å–ª—É—à–∞–µ—Ç —Å–æ–±—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É –Ω–∞ ESC –∏ –≤—ã–∑—ã–≤–∞–µ—Ç —Ñ—É–Ω–∫—Ü–∏—é –∑–∞–∫—Ä—ã—Ç–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
    // —Å–æ–±—ã—Ç–∏–µ –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ ESC –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
    document.addEventListener("keydown", (e) => {
      if (e.code === "Escape" && this.modal.classList.contains("active")) {
        this.modalClose();
      }
    });
  }

  // —Å–æ–±—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É –Ω–∞ —ç–ª–µ–º–µ–Ω—Ç –ª–∞–π—Ç–±–æ–∫—Å–∞
  itemHandler(item) {
    if (!this.modal) return false;

    // –µ—Å–ª–∏ —ç–ª–µ–º–µ–Ω—Ç —Å–æ—Å—Ç–æ–∏—Ç –≤ –≥—Ä—É–ø–ø–µ –∏ –∏–º—è –≥—Ä—É–ø–ø—ã –Ω–µ –ø—É—Å—Ç–∞—è —Å—Ç—Ä–æ–∫–∞
    if (item.dataset.lbItemGroup && item.dataset.lbItemGroup !== "") {
      this.renderGroupItems(item);
    } else {
      // –µ—Å–ª–∏ —ç–ª–µ–º–µ–Ω—Ç—ã –Ω–µ –≤ –≥—Ä—É–ø–ø–µ
      this.renderItem(item);
    }
  }

  // –æ—Ç—Ä–∏—Å–æ–≤–∫–∞ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  renderModal() {
    const body = document.querySelector("body");
    const html = `
		 <div class="modal-lb">
			 <div class="modal-lb__inner">
			     <!-- modal item -->
				 <div class="modal-lb__item-wrap"></div>
				 
				 <!-- modal footer -->
				 <div class="modal-lb__footer">
				     <!-- description -->
				    <div class="modal-lb__item-desc"></div>
				 </div>
				 
				
			 </div>
			 
			<!-- close button -->
			<div class="modal-lb__close modal-lb__close-btn">&times</div>
				 
			 <!-- close background -->
			 <div class="modal-lb__close modal-lb__close-bg"></div>
		 </div>
		`;

    body.insertAdjacentHTML("beforeend", html);

    // –¥–æ–±–∞–≤–ª—è–µ–º –≤ —Å–≤–æ–π—Å—Ç–≤–æ —ç–ª–µ–º–µ–Ω—Ç –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
    this.modal = document.querySelector(".modal-lb");

    // —Ñ—É–Ω–∫—Ü–∏—è –∑–∞–∫—Ä—ã—Ç–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
    this.modalClose();
  }

  // —Å–æ–∑–¥–∞–µ–º –Ω–µ—Å–∫–æ–ª—å–∫–æ —ç–ª–µ–º–µ–Ω—Ç–æ–≤
  renderGroupItems(item) {
    const groupName = item.dataset.lbItemGroup;
    const items = [...this.items];
    let count = 0;
    this.groupItems = items.reduce((prev, curr) => {
      if (curr.dataset.lbItemGroup === groupName) {
        curr.dataset.lbItemIndex = `${count}`;
        prev.push(curr);
        count++;
      }
      return prev;
    }, []);

    // –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –∏–∑ –≥—Ä—É–ø–ø—ã –≤ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
    this.renderItem(item);

    // –æ—Ç—Ä–∏—Å–æ–≤–∫–∞ –∫–Ω–æ–ø–æ–∫ –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏—è –º–µ–∂–¥—É –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è–º–∏ –≤ –º–æ–¥–∞–ª—å–Ω–æ–º –æ–∫–Ω–µ
    this.renderNextPrev();

    // —Ä–µ–Ω–¥–µ—Ä –ø—Ä–µ–≤—å—é –≥–∞–ª–µ—Ä–µ–∏
    this.renderPreview();
  }

  // –≤—Å—Ç–∞–≤–ª—è–µ–º —ç–ª–µ–º–µ–Ω—Ç –≤ modal-lb__item-wrap
  renderItem(item) {
    const data = {
      tagName: item.tagName.toLowerCase(),
      name: item.dataset.lbItemName || false,
      description: item.dataset.lbItemDesc || false,
      src: item.dataset.lbItemSrc || false,
      group: item.dataset.lbItemGroup || false,
      index: item.dataset.lbItemIndex || false
    };
    const html = this.createEl(data.tagName, data.src, data.index, [
      "modal-lb__item"
    ]);
    const wrapper = document.querySelector(".modal-lb__item-wrap");

    // –¥–æ–±–∞–≤–ª—è–µ–º –æ–ø–∏—Å–∞–Ω–∏–µ –∫ —ç–ª–µ–º–µ–Ω—Ç—É
    this.renderDescription(data);

    wrapper.innerHTML = "";
    wrapper.insertAdjacentElement("afterbegin", html);

    this.modalShow(data);

    // —Å–æ–±—ã—Ç–∏—è –ø—Ä–∏ –∫–∞—Å–∞–Ω–∏–∏
    this.itemEvents(html);
  }

  // –¥–æ–±–∞–≤–ª—è–µ—Ç –æ–ø–∏—Å–∞–Ω–∏–µ –≤ –≤–µ—Ä—Å—Ç–∫—É –¥–ª—è —ç–ª–µ–º–µ–Ω—Ç–∞
  renderDescription(data) {
    const wrapper = this.modal.querySelector(".modal-lb__item-desc");
    wrapper.innerHTML = "";

    // –µ—Å–ª–∏ –µ—Å—Ç—å –∏–º—è —Ç–æ –¥–æ–±–∞–≤–∏—Ç—å
    if (data.name) {
      wrapper.insertAdjacentHTML(
        "afterbegin",
        ` <div class="modal-lb__item-desc-title">${data.name}</div>`
      );
    }

    // –µ—Å–ª–∏ –µ—Å—Ç—å –æ–ø–∏—Å–∞–Ω–∏–µ —Ç–æ –¥–æ–±–∞–≤–∏—Ç—å
    if (data.description) {
      wrapper.insertAdjacentHTML(
        "beforeend",
        ` <div class="modal-lb__item-desc-text">${data.description}</div>`
      );
    }
  }

  // –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –ø—Ä–µ–≤—å—é –≥–∞–ª–µ—Ä–µ–∏
  renderPreview() {
    if (this.preview) {
      const items = this.groupItems;
      const modalFooter = this.modal.querySelector(".modal-lb__footer");

      // –µ—Å–ª–∏ –µ—Å—Ç—å –±–ª–æ–∫ –ø—Ä–µ–≤—å—é —Ç–æ —É–¥–∞–ª–∏—Ç—å —ç—Ç–æ—Ç –±–ª–æ–∫
      this.removePreview();

      const previews = items
        .map(
          (item, index) =>
            `<div class="modal-lb__preview-item" data-lb-preview-index="${index}" style="background-image:url('${item.src}')"></div>`
        )
        .join("");
      const html = `<div class="modal-lb__preview">${previews}</div>`;

      // –¥–æ–±–∞–≤–ª—è–µ–º –±–ª–æ–∫ –ø—Ä–µ–≤—å—é –≤ —Ñ—É—Ç–µ—Ä
      modalFooter.insertAdjacentHTML("beforeend", html);

      // –ø–µ—Ä–µ–∫–ª—é—á–∞–µ—Ç –∞–∫—Ç–∏–≤–Ω—ã–π –∫–ª–∞—Å—Å –ø—Ä–µ–≤—å—é –¥–ª—è —Ç–µ–∫—É—â–µ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –≤ –º–æ–¥–∞–ª—å–Ω–æ–º –æ–∫–Ω–µ
      this.switchActivePreview();

      // —Å–æ–±—ã—Ç–∏–µ –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –ø—Ä–µ–≤—å—é
      this.previewHandler();

      // —Å–º–µ—â–µ–Ω–∏–µ –ø—Ä–µ–≤—å—é –¥–æ –∞–∫—Ç–∏–≤–Ω–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –≤ –ø–æ–ª–æ—Å–µ –ø—Ä–µ–≤—å—é
      this.scrollToActivePreview();
    }
  }

  // –¥–æ–±–∞–≤–ª—è–µ—Ç –∫–ª–∞—Å—Å active –¥–ª—è —Ç–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –∫–æ—Ç–æ—Ä—ã–π –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç—Å—è
  switchActivePreview() {
    if (this.preview) {
      const items = this.modal.querySelectorAll(".modal-lb__preview-item");
      const currentItemIndex = this.modal.querySelector(
        "[data-lb-current-item-index]"
      ).dataset.lbCurrentItemIndex;

      // —É–¥–∞–ª—è–µ—Ç —É –≤—Å–µ—Ö –ø—Ä–µ–≤—å—é –∫–ª–∞—Å—Å active
      items.forEach((item) => item.classList.remove("active"));

      // –Ω–∞—Ö–æ–¥–∏–º —ç–ª–µ–º–µ–Ω—Ç –ø—Ä–µ–≤—å—é —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏–π –∏–Ω–¥–µ–∫—Å—É —Ç–µ–∫—É—â–µ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –≤ –º–æ–¥–∞–ª—å–Ω–æ–º –æ–∫–Ω–µ
      const currentItem = this.modal.querySelector(
        `[data-lb-preview-index="${currentItemIndex}"]`
      );

      // –¥–æ–±–∞–≤–ª—è–µ–º –∫–ª–∞—Å—Å active –∫ —ç–ª–µ–º–µ–Ω—Ç—É
      currentItem.classList.add("active");

      // —Å–∫—Ä–æ–ª–ª –±–ª–æ–∫–∞ preview –¥–æ –æ–±–ª–∞—Å—Ç–∏ –≤–∏–¥–∏–º–æ—Å—Ç–∏ –∞–∫—Ç–∏–≤–Ω–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –∏ –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ –ø–æ —Ü–µ–Ω—Ç—Ä—É
      currentItem.scrollIntoView({ behavior: "smooth", inline: "center" });
    }
  }

  // —Å–æ–±—ã—Ç–∏—è –¥–ª—è —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –ø—Ä–µ–≤—å—é
  previewHandler() {
    const groupItems = this.groupItems;
    const wrapper = this.modal.querySelector(".modal-lb__preview");
    const previews = wrapper.querySelectorAll(".modal-lb__preview-item");

    // –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –ø—Ä–µ–≤—å—é –ø–µ—Ä–µ–∫–ª—é—á–∏—Ç—å –æ—Å–Ω–æ–≤–Ω–æ–π —ç–ª–µ–º–µ–Ω—Ç –∏ –ø–µ—Ä–µ–∫–ª—é—á–∏—Ç—å –∞–∫—Ç–∏–≤–Ω–æ–µ –ø—Ä–µ–≤—å—é
    previews.forEach((item) =>
      item.addEventListener("click", () => {
        // –µ—Å–ª–∏ —ç–ª–µ–º–µ–Ω—Ç –Ω–∞ –∫–æ—Ç–æ—Ä–æ–º –±—ã–ª –∫–ª–∏–∫ –Ω–µ –∞–∫—Ç–∏–≤–Ω—ã–π (.active)
        if (!item.classList.contains("active")) {
          const index = item.dataset.lbPreviewIndex;

          // –µ—Å–ª–∏ –∫–ª–∏–∫–Ω—É–ª–∏ –Ω–∞ –ø—Ä–µ–≤—å—é —Ç–æ –∑–∞–º–µ–Ω–∏—Ç—å —ç–ª–µ–º–µ–Ω—Ç –≤ –º–æ–¥–∞–ª—å–Ω–æ–º –æ–∫–Ω–µ
          this.renderItem(groupItems[index]);

          // –ø–µ—Ä–µ–∫–ª—é—á–∏—Ç—å –∞–∫—Ç–∏–≤–Ω—ã–π —ç–ª–µ–º–µ–Ω—Ç –ø—Ä–µ–≤—å—é –Ω–∞ —Ç–æ—Ç –∫–æ—Ç–æ—Ä—ã–π —Å–µ–π—á–∞—Å –≤—ã–≤–æ–¥–∏—Ç—Å—è –≤ –∫–æ–Ω–µ
          this.switchActivePreview();
        }
      })
    );

    // –µ—Å–ª–∏ —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ –±–ª–æ–∫–∞ –±–æ–ª—å—à–µ –æ–±–ª–∞—Å—Ç–∏ –≤–∏–¥–∏–º–æ—Å—Ç–∏ —Ç–æ —Å–¥–µ–ª–∞—Ç—å –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ –ø–æ –ª–µ–≤–æ–º—É –∫—Ä–∞—é
    if (wrapper.clientWidth + 80 < wrapper.scrollWidth) {
      wrapper.style.justifyContent = "start";

      // —Å–º–µ—â–µ–Ω–∏–µ –º—ã—à–∫–æ–π –∏ —Å–∫—Ä–æ–ª–ª
      this.previewEvents(wrapper);
    }
  }

  // —É–¥–∞–ª—è–µ—Ç –±–ª–æ–∫ preview
  removePreview() {
    const modalFooter = this.modal.querySelector(".modal-lb__footer");
    const modalPreview = modalFooter.querySelector(".modal-lb__preview");

    if (modalPreview) modalPreview.remove();
  }

  // –¥–æ–±–∞–≤–ª—è–µ–º –∫–Ω–æ–ø–∫–∏ –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏—è –≤ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
  renderNextPrev() {
    const arrowLeft = this.arrowLeft === "" ? "&#x203a;" : this.arrowLeft;
    const arrowRight = this.arrowRight === "" ? "&#x2039;" : this.arrowRight;

    // —Å–ª–µ–¥—É—é—â–∏–π —ç–ª–µ–º–µ–Ω—Ç
    const next = this.createEl("div", false, false, [
      "modal-lb__nav",
      "modal-lb__item-next"
    ]);
    next.addEventListener("click", () => this.nextPrevHandler("next"));
    next.innerHTML = arrowLeft;
    this.modal.insertAdjacentElement("beforeend", next);

    // –ø—Ä–µ–¥—ã–¥—É—â–∏–π —ç–ª–µ–º–µ–Ω—Ç
    const prev = this.createEl("div", false, false, [
      "modal-lb__nav",
      "modal-lb__item-prev"
    ]);
    prev.addEventListener("click", () => this.nextPrevHandler("prev"));
    prev.innerHTML = arrowRight;
    this.modal.insertAdjacentElement("beforeend", prev);
  }

  // —É–¥–∞–ª—è–µ–º –∫–Ω–æ–ø–∫–∏ –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
  removeNextPrev() {
    const navs = this.modal.querySelectorAll(".modal-lb__nav");

    if (navs.length > 0) {
      navs.forEach((nav) => nav.remove());
    }
  }

  // –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –≥—Ä—É–ø–ø—ã
  nextPrevHandler(direction) {
    const groupItems = this.groupItems;
    const currentItem = this.modal.querySelector(".modal-lb__item");
    const currentIndex = +currentItem.dataset.lbCurrentItemIndex;

    // –ø—Ä–µ–¥—ã–¥—É—â–∏–π —ç–ª–µ–º–µ–Ω—Ç
    if (direction === "prev") {
      if (currentIndex + 1 <= groupItems.length && currentIndex !== 0) {
        this.renderItem(groupItems[currentIndex - 1]);
      } else {
        this.renderItem(groupItems[groupItems.length - 1]);
      }
    }

    // —Å–ª–µ–¥—É—é—â–∏–π —ç–ª–µ–º–µ–Ω—Ç
    if (direction === "next") {
      if (currentIndex + 1 < groupItems.length) {
        this.renderItem(groupItems[currentIndex + 1]);
      } else {
        this.renderItem(groupItems[0]);
      }
    }

    // –ø–µ—Ä–µ–∫–ª—é—á–∞–µ–º—Å—è –º–µ–∂–¥—É –∞–∫—Ç–∏–≤–Ω—ã–º –ø—Ä–µ–≤—å—é
    this.switchActivePreview();
  }

  // —Å–æ–∑–¥–∞–µ–º —ç–ª–µ–º–µ–Ω—Ç
  createEl(selector, src = false, index = false, cls = []) {
    const el = document.createElement(`${selector}`);

    // –µ—Å–ª–∏ –µ—Å—Ç—å src —Ç–æ –¥–æ–±–∞–≤–∏—Ç—å –∫ —ç–ª–µ–º–µ–Ω—Ç—É
    if (src) {
      el.setAttribute("src", `${src}`);
    }

    // –¥–ª—è –≥—Ä—É–ø–ø—ã - –µ—Å–ª–∏ –µ—Å—Ç—å index (–ø–æ—Ä—è–¥–∫–æ–≤—ã–π –Ω–æ–º–µ—Ä —ç–ª–µ–º–µ–Ω—Ç–∞ –≤ –º–∞—Å—Å–∏–≤–µ –≥—Ä—É–ø–ø—ã)
    // —Ç–æ –¥–æ–±–∞–≤–∏—Ç—å –∞—Ç—Ä–∏–±—É—Ç —Å –∏–Ω–¥–µ–∫—Å–æ–º
    if (index) {
      el.dataset.lbCurrentItemIndex = index;
    }

    if (cls.length > 0) {
      cls.forEach((name) => el.classList.add(name));
    }

    return el;
  }

  // –æ—Ç–∫—Ä—ã—Ç—å –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
  modalShow() {
    if (this.modal) {
      this.modal.classList.add("active");

      if (this.overflow) {
        document.querySelector("body").style.overflow = "hidden";
      }
    }
  }

  // –∑–∞–∫—Ä—ã—Ç—å –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
  modalClose() {
    if (this.modal) {
      const closes = this.modal.querySelectorAll(".modal-lb__close");

      closes.forEach((close) =>
        close.addEventListener("click", () => {
          this.reset();
        })
      );

      this.reset();
    }
  }

  // —Å–±—Ä–æ—Å –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤ —ç–ª–µ–º–µ–Ω—Ç–æ–º –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
  reset() {
    // —É–¥–∞–ª—è–µ–º –∫–ª–∞—Å—Å—ã —É –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
    this.modal.classList.remove("active");
    this.modal.classList.remove("clear");

    // —É–¥–∞–ª—è–µ–º –∫–Ω–æ–ø–∫–∏ –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
    this.removeNextPrev();

    // –ø—Ä–æ–≤–µ—Ä—è–µ—Ç –µ—Å–ª–∏ –µ—Å—Ç—å –ø—Ä–µ–≤—å—é –±–ª–æ–∫ —Ç–æ —É–¥–∞–ª—è–µ—Ç —ç—Ç–æ—Ç –±–ª–æ–∫
    this.removePreview();

    // —Å–±—Ä–æ—Å –¥–∞–Ω–Ω—ã—Ö –æ –≥—Ä—É–ø–ø–µ —ç–ª–µ–º–µ–Ω—Ç–æ–≤
    this.groupItems = null;

    // —Å–±—Ä–æ—Å —Å—Ç–∏–ª–µ–π –¥–ª—è body
    if (this.overflow) {
      document.querySelector("body").style.overflow = "auto";
    }
  }

  // –¥–µ–π—Å—Ç–≤–∏—è –ø—Ä–∏ —Å–≤–∞–π–ø–µ
  itemEvents(item) {
    // –∏—Å—Ç–æ—á–Ω–∏–∫ https://stackoverflow.com/questions/2264072/detect-a-finger-swipe-through-javascript-on-the-iphone-and-android

    // –≤–∫–ª—é—á–µ–Ω–∏–µ / –æ—Ç–∫–ª—é—á–µ–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ —É–ø—Ä–∞–≤–ª–µ–Ω–∏—è
    item.addEventListener("click", () => {
      this.modal.classList.toggle("clear");
    });

    let startX;
    let xDown = null;

    item.addEventListener(
      "touchstart",
      (evt) => {
        const firstTouch = getTouches(evt)[0];

        xDown = firstTouch.clientX;
        startX = firstTouch.clientX;
      },
      false
    );

    item.addEventListener(
      "touchmove",
      (evt) => {
        evt.preventDefault(); // –±–ª–æ–∫–∏—Ä—É–µ—Ç —Å–∫—Ä–æ–ª–ª –ø—Ä–∏ –æ—Ç–∫—Ä—ã—Ç–æ–º –º–æ–¥–∞–ª—å–Ω–æ–º –æ–∫–Ω–µ –Ω–∞ —Ç–µ–ª–µ—Ñ–æ–Ω–µ

        let x = evt.touches[0].clientX;

        if (!xDown) {
          return;
        }

        let xUp = evt.touches[0].clientX;
        let xDiff = xDown - xUp;

        /* left swipe */
        if (x + 100 < startX) {
          if (xDiff > 0) {
            this.nextPrevHandler("next");
          }
          xDown = null;
        }

        /* right swipe */
        if (x - startX > 100) {
          if (xDiff < 0) {
            this.nextPrevHandler("prev");
          }
          xDown = null;
        }
      },
      false
    );

    function getTouches(evt) {
      return (
        evt.touches || evt.originalEvent.touches // browser API
      ); // jQuery
    }
  }

  // –ø–æ–≤–µ–¥–µ–Ω–∏–µ –ø–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–Ω–∏–µ–º –º—ã—à–∏
  previewEvents(el) {
    //example  https://codepen.io/kasap-victor/pen/NWjGYXv?editors=0010

    let isDown = false;
    let scrollLeft;
    let startX;

    // –ø—Ä–∏ —Å–∫—Ä–æ–ª–ª–µ —Å–¥–≤–∏–≥–∞–µ—Ç –±–ª–æ–∫ –≤–ª–µ–≤–æ –∏–ª–∏ –ø—Ä–∞–≤–æ
    el.addEventListener("wheel", (e) => {
      el.scrollLeft += e.deltaY;
      startX = el.scrollLeft;
    });

    // –ª–µ–≤–∞—è –∫–ª–∞–≤–∏—à–∞ –º—ã—à–∫–∏ –Ω–∞–∂–∞—Ç–∞
    el.addEventListener("mousedown", (e) => {
      isDown = true;
      startX = e.pageX; // –Ω–∞—á–∞–ª—å–Ω–∞—è –ø–æ–∑–∏—Ü–∏—è –∫—É—Ä—Å–æ—Ä–∞ –æ—Ç–Ω–æ—Å–∏—Ç–µ–ª—å–Ω–æ –¥–æ–∫—É–º–µ–Ω—Ç–∞
      scrollLeft = el.scrollLeft; // —Å–º–µ—â–µ–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–∞
    });

    // –µ—Å–ª–∏ –∫—É—Ä—Å–æ—Ä –≤–Ω–µ –∑–æ–Ω—ã –æ–±–ª–∞—Å—Ç–∏ –ø—Ä–µ–≤—å—é —Ç–æ –æ—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –ø–µ—Ä–µ–º–µ—â–µ–Ω–∏–µ
    el.addEventListener("mouseleave", () => {
      isDown = false;
    });

    // –ª–µ–≤–∞—è –∫–ª–∞–≤–∏—à–∞ –º—ã—à–∫–∏ –æ—Ç–∂–∞—Ç–∞
    el.addEventListener("mouseup", (e) => {
      isDown = false;
    });

    // –¥–≤–∏–∂–µ–Ω–∏–µ –º—ã—à–∏ –Ω–∞–¥ –±–ª–æ–∫–æ–º –ø—Ä–µ–≤—å—é
    el.addEventListener(
      "mousemove",
      (e) => {
        if (!isDown) return;

        e.preventDefault();

        const x = e.pageX; // –ø–æ–∑–∏—Ü–∏—è —ç–ª–µ–º–µ–Ω—Ç–∞ –Ω–∞ –¥–æ–∫—É–º–µ–Ω—Ç–µ -> –ø–æ—Å—Ç–æ—è–Ω–Ω–æ –æ–±–Ω–æ–≤–ª—è–µ—Ç—Å—è –ø—Ä–∏ –ø–µ—Ä–µ–º–µ—â–µ–Ω–∏–∏ –∫—É—Ä—Å–æ—Ä–∞
        // startX - –Ω–∞—á–∞–ª—å–Ω–∞—è –ø–æ–∑–∏—Ü–∏—è –∫—É—Ä—Å–æ—Ä–∞ –æ—Ç–Ω–æ—Å–∏—Ç–µ–ª—å–Ω–æ –¥–æ–∫—É–º–µ–Ω—Ç–∞
        const walk = (x - startX) * 3; // —Ä–∞–∑–Ω–∏—Ü–∞ —Å–º–µ—â–µ–Ω–∏—è –º–µ–∂–¥—É –Ω–∞—á–∞–ª–æ–º –∫–ª–∏–∫–∞ –∏ –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç—ã –¥–≤–∏–∂–µ–Ω–∏—è –•

        // scrollLeft - –∑–Ω–∞—á–µ–Ω–∏–µ —Å–º–µ—â–µ–Ω–∏—è –≤ –º–æ–º–µ–Ω—Ç –Ω–∞–∂–∞—Ç–∏—è –∫–Ω–æ–ø–∫–∏ –º—ã—à–∏
        el.scrollLeft = scrollLeft - walk; // –º–µ–Ω—è–µ—Ç —Å–º–µ—â–µ–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–∞ –≤ –º–æ–º–µ–Ω—Ç –¥–≤–∏–∂–µ–Ω–∏—è –≤—ã—Å—á–∏—Ç—ã–≤–∞—è —Ä–∞–∑–Ω–∏—Ü—É
      },
      true
    );
  }

  // —Å–º–µ—â–µ–Ω–∏–µ –ø—Ä–≤–µ—å—é –¥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –∫–æ—Ç–æ—Ä—ã–π –∞–∫—Ç–∏–≤–Ω—ã–π
  scrollToActivePreview() {
    const wrapper = this.modal.querySelector(".modal-lb__preview");
    const item = wrapper.querySelector(".modal-lb__preview-item.active");

    wrapper.scrollLeft = item.offsetLeft - wrapper.scrollWidth * 0.111;
  }
}

const config = {
  preview: true, // –ø–æ–∫–∞–∑–∞—Ç—å –º–∏–Ω–∏–∞—Ç—é—Ä—ã
  overflow: true, // –≤–∫–ª—é—á–∏—Ç—å overflow:hidden –¥–ª—è body –∫–æ–≥–¥–∞ –æ—Ç–∫—Ä—ã—Ç–∞ –≥–∞–ª–µ—Ä–µ—è
  arrowLeft: "üëâ", // —Å—Ç—Ä–µ–ª–∫–∞ –≤–ª–µ–≤–æ
  arrowRight: "üëà" // —Å—Ç—Ä–µ–ª–∫–∞ –≤–ø—Ä–∞–≤–æ
};
new LightBox(config);
